.Quest__title {
    font-weight: bold;
}

.Quest__text {
    font-size: 24px;
    line-height: 1.3em;
    color: #000;
    padding: 0 48px 24px 48px;
}

.Quest__answers {
    background-color: #f0f0f0;
    padding: 24px 48px 24px 48px;
}
.Quest__final-actions,
.Quest__answers {
    padding: 24px 48px 24px 48px;
}

.Quest__action-container {
    width: 100%;
    box-sizing: border-box;
    border-radius: 12px;
    border: 2px solid #CCC;
    background-color: #FFF;
    color: #000;
    text-align: left;
}

.Quest__action {
    display: block;
    width: 100%;
    padding: 12px;
    color: inherit;
    text-align: inherit;
}
.Quest__action + .Quest__action {
    margin-top: 24px;
}

.Quest__ml {
    white-space: pre-wrap;
}

.Quest__user-input-container {
    padding: 12px;
}
.Quest__user-input-submit {
    margin-left: 24px;
}

.Quest__registration {
    margin: 12px 0;
}
