.AppHeader {
    text-align: right;
    line-height: 48px;
    font-size: 32px;

    background-image: url("./oklogo.svg");
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: auto 48px;
}


.AppHeader__fullscreen {
    color: #ee8208;
    font-size: inherit;
    border: 0;
    background-color: transparent;
    vertical-align: bottom;
}

.AppHeader_small {
    line-height: 24px;
    background-size: auto 24px;
    font-size: 16px;
}
